<template>
  <div class="activityList">
    <div
      class="item"
      v-for="(item, i) in list"
      :key="i"
      @click="toDetail(item)"
    >
      <div>活动名称：{{ item.hdmc }}</div>
      <div>
        <span>活动类型：{{ item.lb }}</span>
        <span class="right">
          <span class="red">{{ item.isBm == 1 ? "已报名" : item.bmrssx > item.ybmgs ? "未报名" : "已满" }}</span>
          <van-icon name="arrow" />
        </span>
      </div>
      <div>活动时间：{{ `${item.kssj||'2019/06/08'}—${item.jssj||'2019/06/08'}` }}</div>
    </div>
  </div>
</template>

<script>
import {hdxxquerydata4list} from '../assets/js/api';
export default {
  data() {
    return {
      list: [],
      dspToken: localStorage.dspToken
    };
  },
  mounted() {
    hdxxquerydata4list({
      dspToken: this.dspToken
    }).then(res => {
      if (res.code.status === '1') {
        this.list = res.data;
      } else {
        this.$toast('获取数据失败');
      }
    });
  },
  methods: {
    toDetail(v) {
      this.$router.push({
        path: '/activityDetail',
        query: {
          id: v.hdjhId
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.activityList {
  .item {
    width: 704px;
    height: 195px;
    background-color: #ffffff;
    box-shadow: 3px 4px 9px 1px rgba(1, 88, 155, 0.05);
    border-radius: 8px;
    margin: 0 auto;
    margin-top: 23px;
    position: relative;
    padding: 31px 30px 26px 33px;
    box-sizing: border-box;
    &::before {
      content: "";
      width: 5px;
      height: 195px;
      background-color: #3198ed;
      position: absolute;
      left: 0;
      top: 0;
      border-top-left-radius: 50px;
      border-bottom-left-radius: 50px;
    }
    > div:first-child {
      font-size: 34px;
      color: #333333;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    > div:nth-child(2) {
      font-size: 30px;
      color: #888888;
      margin-top: 29px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 30px;
    }
    > div:last-child {
      font-size: 30px;
      color: #888888;
      margin-top: 4px;
    }
    .red {
      color: #d42a2b;
      font-size: 36px;
      margin-right: 10px;
    }
  }
}
</style>



